export const collision = writable(wall)
export const Task0 = writable(task0)
export const Task1 = writable(task1)
export const Task2 = writable(task2)
export const Info = writable(information)
export const Danger = writable(danger)

export const walk = writable(false)

export let life = writable(100)

export const ranking = writable([])

export const infoTasks0 = writable("INCOMPLETO!");
export const infoTasks1 = writable("INCOMPLETO!");
export const infoTasks2 = writable("INCOMPLETO!");

export const Nome = writable("");
export const totalPoints = writable(0);
export const taskOrder = writable({
    t0: true,
    t1: false,
    t2: false
});     


ranking.subscribe(v => {
    const user = v.at(-1);
    if (!user) return;
    const formData = new FormData();
    formData.append('Nome', user.nome);
    formData.append('pontos', user.pontos);

    fetch('http://localhost:8001/adicionar.php', {
        method: 'post',
        body: formData
    }); // non blocking
})